// project list

.project-list-table{
    border-collapse:separate; 
    border-spacing:0 12px; 
  tr{
    background-color: $card-bg;
  }
}


body[data-layout-mode="dark"] {
  .project-list-table{
    border-collapse:separate; 
    border-spacing:0 12px; 
  tr{
    background-color: $gray-dark-200;
  }
}
}